import React, { useState, useEffect, useContext } from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { ConsoleContextStore } from '../ConsoleContext';
import { Button, TextField, Typography } from "@mui/material";

export default function LinkList() {
    const { boardlist, setBoardlist, postStatus, update } = useContext(ConsoleContextStore)
    const [openingVideo, setOpeningVideo] = useState()
    const [boardVideo, setBoardVideo] = useState()
    const [askBoard, setAskBoard] = useState()
    const [cheerBoard, setCheerBoard] = useState()
    const [suggestBoard, setSuggestBoard] = useState()

    const handleOpeningVideo = (event) => {
        setOpeningVideo(event.target.value);
    }
    const handleBoardVideo = (event) => {
        setBoardVideo(event.target.value);
    }
    const handleAskBoard = (event) => {
        setAskBoard(event.target.value);
    }
    const handleCheerBoard = (event) => {
        setCheerBoard(event.target.value);
    }
    const handleSuggestBoard = (event) => {
        setSuggestBoard(event.target.value);
    }
    const postOpeningVideo = () => {
        update({ type_: "openingVideo", url: openingVideo })
    }
    const postBoardVideo = () => {
        update({ type_: "boardVideo", url: boardVideo })
    }
    const postCheer = () => {
        update({ type_: "cheerBoard", url: cheerBoard })
    }
    const postAsk = () => {
        update({ type_: "askBoard", url: askBoard })
    }
    const postSuggest = () => {
        update({ type_: "suggestBoard", url: suggestBoard })
    }


    useEffect(function () {

        boardlist ? boardlist.forEach(element => {
            switch (element.type_) {
                case 'openingVideo':
                    setOpeningVideo(element.url)
                    break;
                case 'cheerBoard':
                    setCheerBoard(element.url)
                    break;
                case 'suggestBoard':
                    setSuggestBoard(element.url)
                    break;
                case 'boardVideo':
                    setBoardVideo(element.url)
                    break;
                case 'askBoard':
                    setAskBoard(element.url)
                    break;
                default:
                    console.log(`Sorry, we are out of ${expr}.`);
            }
        }) : "";
    }, [postStatus, boardlist]);

    return (
        <div>
            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>시작 동영상 : </Typography>
                    <Typography>{openingVideo}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TextField size="small" onChange={handleOpeningVideo}
                        value={openingVideo}
                        variant="outlined"
                    />
                    <Button onClick={postOpeningVideo}>Update</Button>
                </AccordionDetails>
            </Accordion>
            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>전광판 동영상 : </Typography>
                    <Typography>{boardVideo}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TextField size="small" onChange={handleBoardVideo}
                        value={boardVideo}
                        variant="outlined" />
                    <Button onClick={postBoardVideo}>Update</Button>
                </AccordionDetails>
            </Accordion>
            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>박형준에게 바란다 : </Typography>
                    <Typography>{askBoard}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TextField size="small" onChange={handleAskBoard}
                        value={askBoard}
                        variant="outlined" />
                    <Button onClick={postAsk}>Update</Button>
                </AccordionDetails>
            </Accordion>
            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>응원하기 게시판 : </Typography>
                    <Typography>{cheerBoard}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TextField size="small" onChange={handleCheerBoard}
                        value={cheerBoard}
                        variant="outlined"
                    />
                    <Button onClick={postCheer}>Update</Button>
                </AccordionDetails>
            </Accordion>
            <Accordion >
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>정책제안 게시판 : </Typography>
                    <Typography>{suggestBoard}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <TextField size="small" onChange={handleSuggestBoard}
                        value={suggestBoard}
                        variant="outlined"
                    />
                    <Button onClick={postSuggest}>Update</Button>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
